import React from 'react';
import { useParams } from 'react-router-dom';
import PdfViewer from '../components/project/Project';

const PdfPage = () => {
  // Use `useParams` to access the dynamic parts of the route.
  let { pdfName } = useParams();

  // Construct the path to the PDF file based on the route parameter.
  // Ensure the base path matches where your PDFs are stored relative to the public directory.
  const pdfPath = `/assets/pdfs/${pdfName}.pdf`;

  return (
    <div>
      <h1>PDF Viewer</h1>
      <PdfViewer pdfUrl={pdfPath} />
    </div>
  );
};

export default PdfPage;