import React from 'react';
import './index.scss';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Header from './components/header/Header';
import Footer from './components/footer/Footer';
import HomePage from './pages/HomePage';
import Project from './components/project/Project';
import PdfPage from './pages/PdfPage';

function App() {
  const pdfFiles = [
    'assets/pdfs/memoirfinal.pdf'
    // Add more paths as needed
  ];

  return (
    <Router>
      <div className="App">
        <Header />
        <main>
          {/* Use Routes instead of Switch for React Router v6 */}
          <Routes>
            {/* Define the route for the HomePage and nested Project component */}
            <Route path="/" element={
              <>
                <HomePage />
                <Project files={pdfFiles} />
              </>
            } />

            {/* Define the route for the dynamic PDF Viewer */}
            <Route path="/pdf/:pdfName" element={<PdfPage />} />
          </Routes>
        </main>
        <Footer />
      </div>
    </Router>
  );
}

export default App;