import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Document, Page, pdfjs } from 'react-pdf';
import { Link } from 'react-router-dom';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;



const Project = () => {
    const documents = [
        {
          id: 1,
          thumbnail: '/assets/thumbnails/thumbnail1.png',
          pdf: 'memoirfinal.pdf', // Name of the PDF file without the path
        },
        // Add more documents as needed
      ];
    return (
        <Swiper
          spaceBetween={50}
          slidesPerView={1}
          navigation
          pagination={{ clickable: true }}
        >
      {documents.map((doc) => (
        <SwiperSlide key={doc.id}>
          <Link to={`/pdf/${encodeURIComponent(doc.pdf)}`}>
            <img src={doc.thumbnail} alt={`Document ${doc.id}`} style={{ width: '100%', cursor: 'pointer' }} />
          </Link>
        </SwiperSlide>
      ))}
    </Swiper>
  );
};
export default Project;